import './App.css';
import Navbar from './Components/Navbar';
import {BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import React from 'react';
import Home from './Components/pages/Home';
import PokemonList from './Components/pages/PokemonList';
import PokemonDetails from './Components/pages/PokemonDetails';
import { MyPokemon } from './Components/pages/MyPokemon';

function App() {
  return (
    <>
    <Router>
      <Navbar/>
      <Switch>
        <Route path='/tokopedia-pokemon' exact component={Home}/>
        <Route path='/tokopedia-pokemon/PokemonList' exact component={PokemonList}/>
        <Route path='/tokopedia-pokemon/PokemonDetails' exact component={PokemonDetails}/>
        <Route path='/tokopedia-pokemon/MyPokemon' exact component={MyPokemon}/>
      </Switch>
    </Router>
    </>
  );
}

export default App;
