import gql from 'graphql-tag';

export const GET_POKEMONS = gql `
query pokemons($limit: Int, $offset: Int) {
  pokemons(limit: $limit, offset: $offset) {
    count
    next
    previous
    nextOffset
    prevOffset
    status
    message
    results {
      id
      url
      name
      image
     }
  }
}`;

export const gqlVariables = {
    limit: 12,
    offset: 0,
};